.contact-title {
  text-align: center;
  color: black;
  font-weight: bolder;
  margin-bottom: 2rem;
}

div.contact-page-container {
  display: flex;
  width: auto;
}

.statement-container {
  margin-top: 10rem;
  margin-right: 8rem;
}

.statement-container p {
  margin-top: 6vh;
  margin-right: 1rem;
  margin-left: 1rem;
}

.contact {
  padding-top: 2rem;
  display: flex;
  flex-direction: row;
}

.form-container {
  margin-top: 5rem;
  margin-left: 0;
  color: black!important;
}

input[type=text], select, textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid #fffd0e;
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
  color: black!important;
}

.main-form {
  margin: 1rem;
  margin-left: 10rem;

}

.hello {
  padding-top: 6rem;
  margin-left: 0;
}

.form input {
  display: block;
  margin-top: 5px;
  margin-bottom: 5px;
  width: 30rem;
}

.message {
  width: 30rem;
  height: 30vh;
}

textarea.message {
  overflow-wrap: normal !important;
}

.submit:hover {
  color: black;
  box-shadow: 0 6px 40px #15e231, 0 6px 15px #999;
  transition: transform ease-in-out 0.25s;
  transform: scale(1.25);
}

p {
  display: inline;
}

.phone {
  color: #007bff;
  cursor: pointer;
  padding-left: 1rem;
}

.phone:hover {
  text-decoration: underline;
  color: #236197;
}

.email{
  padding-left: 22px;
}

@media only screen and (max-width: 1000px) {

  .form,
  .form input,
  .message {
    width: 80%;
    margin: 2rem;
  }

}

@media only screen and (max-width: 1000px) {

  .statement-container {
    margin-top: 2rem;
    margin-right: 1rem;
  }

  .form,
  .form input,
  .message {
    width: 95%;
    margin: 0;
  }

  div.contact-page-container {
    flex-direction: column;
  }
  .statement-container p{
    margin-top: 6vh;
    margin-right: 1rem;
    margin-left: 1rem;
    text-align: center;
  }


}