
.tabtn:hover {
  background: black !important;
  color: white !important;
  box-shadow: 0 6px 40px #041106, 0 6px 15px #0c0707;
  transition: transform ease-in-out 0.2s;
  transform: scale(1.15);
}

.tabtn {
  background: #fffd0e!important;
  color: #130b0b !important;
  width: 10rem;
  text-align: center;
}

.tabrow .tabtn .active {
  background: #130b0b;
}

hr {
  padding: 1rem;
}
